<template>
  <div class="flex">
    <!-- Calendario -->
    <div class="w-3/4">
      <div class="grid grid-cols-7 gap-1">
        <div
          v-for="(dia, index) in dias"
          :key="index"
          @click="seleccionarDia(dia)"
          class="p-2 border text-center cursor-pointer"
          :class="{
            'bg-red-500 text-white': dia.turno === 1,
            'bg-green-500 text-white': dia.turno === 2,
            'bg-blue-500 text-white': dia.turno === 3,
            'bg-black text-white': dia.turno === 0,
            'ring-4 ring-yellow-400': diaSeleccionado && dia.fecha === diaSeleccionado.fecha
          }"
        >
          {{ dia.fecha.getDate() }}
        </div>
      </div>

      <!-- Leyenda -->
      <div class="mt-4">
        <p><span class="inline-block w-4 h-4 bg-red-500"></span> Primer turno</p>
        <p><span class="inline-block w-4 h-4 bg-green-500"></span> Segundo turno</p>
        <p><span class="inline-block w-4 h-4 bg-blue-500"></span> Tercer turno</p>
        <p><span class="inline-block w-4 h-4 bg-black"></span> Libre</p>
      </div>
    </div>

    <!-- Información adicional -->
    <div class="w-1/4 p-4 border-l">
      <h3 class="font-bold mb-2">Información del día</h3>
      <div v-if="diaSeleccionado">
        <p><strong>Fecha:</strong> {{ diaSeleccionado.fecha.toDateString() }}</p>
        <p><strong>Turno:</strong> {{ obtenerNombreTurno(diaSeleccionado.turno) }}</p>
        <p><strong>Producción que relevás:</strong> {{ calcularProduccionRelevada(diaSeleccionado) }}</p>
        <p><strong>Producción que te releva:</strong> {{ calcularProduccionEntrante(diaSeleccionado) }}</p>
      </div>
      <div v-else>
        <p>Selecciona un día para ver los detalles.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dias: [], // aquí debería estar tu lógica de generación de días con turno y fecha
      diaSeleccionado: null,
    };
  },
  methods: {
    seleccionarDia(dia) {
      this.diaSeleccionado = dia;
    },
    obtenerNombreTurno(turno) {
      switch (turno) {
        case 1: return 'Primer turno';
        case 2: return 'Segundo turno';
        case 3: return 'Tercer turno';
        case 0: return 'Libre';
        default: return 'Desconocido';
      }
    },
    calcularProduccionRelevada(dia) {
  void dia; // evitar warning de ESLint hasta que se use
  return 'Producción anterior';
},
calcularProduccionEntrante(dia) {
  void dia;
  return 'Producción siguiente';
},
  },
};
</script>

<style scoped>
.ring-yellow-400 {
  box-shadow: 0 0 0 4px rgba(251, 191, 36, 0.5);
}
</style>
